@import "../../css-config/mixins.scss";
.footer {
    padding: 5%;
    .footer-content {
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        flex-flow: wrap;
        gap: 20px;
        padding-bottom: 15px;
        @include md {
            justify-content: space-between;
        }
        .col {
            max-width: 300px;
            .title {
                margin-bottom: 20px;
                font-size: 20px;
            }
            .text {
                color: rgba($color: #000000, $alpha: 0.5);
                font-size: 14;
            }
            .btn {
                color: rgba($color: #000000, $alpha: 0.5);
                transition: 0.2s;
                &:hover {
                    color: #fac000;
                }
            }
            
            .c-item {
                display: flex;
                margin-bottom: 10px;
                svg {
                    flex-shrink: 0;
                    font-size: 14px;
                    margin-right: 10px;
                    margin-top: 4px;
                    color: rgba($color: #000000, $alpha: 0.5);
                }
            }
            span {
                &.text {
                    display: flex;
                    margin-bottom: 10px;
                    cursor: pointer;
                }
            }
        }
    }
    .bottom-bar {
        border-top: 1px solid rgba($color: #000000, $alpha: 0.1);
        .bottom-bar-content {
            padding: 20px;
            display: flex;
            align-items: center;
            flex-direction: column;
            text-align: center;
            gap: 10px;
            @include md {
                padding: 0;
                height: 60px;
                max-width: 1200px;
                margin: 0 auto;
                flex-direction: row;
                justify-content: space-between;
                text-align: left;
            }
            .text {
                font-size: 12px;
                color: rgba($color: #000000, $alpha: 0.5);
            }
        }
    }
}